import Component from '../../base/component';

// 
// class 
export const classes = {
  PAGING_ITEM: 'paging-item',
  DISABLED: 'disabled',
  ACTIVE: 'active',
};

export const strings = {};

class Paging extends Component {

  /**
   * pagingControl  {Boolean}
   * recordPerPage  {Boolean}
   * jumpControl  {Boolean}
   * @type {{pagingControl: boolean, recordPerPage: boolean, jumpControl: boolean}}
   */
  static defaultConfig = {
    pagingControl: true,
    recordPerPage: true,
    jumpControl: true,
  };

  static get classes() {
    return classes;
  }

  static get strings() {
    return strings;
  }

  /**
   *  Paging 
   * @param element
   * @param config
   * @returns {Select}
   */
  static mount(element, config) {
    return new Paging(element, config);
  }

  constructor(element, config) {
    super(element, {...Paging.defaultConfig, ...config});
    // 11
    if (this.pageNum === undefined) {
      this.pageNum = 1;
    }

    // 10
    if (this.pageSize === undefined) {
      this.pageSize = 10;
    }

    // 
    // this.totalPages

    // 
    // this.totalCount

    // 
    // this.loadPageData = function() {}

    //  {Boolean}
    // this.pagingControl

    //  {Boolean}
    // this.recordPerPage

    //  {Boolean}
    // this.jumpControl

    // 
    this.adapter = this.createAdapter();

    this.render();
  }

  /**
   * 
   * @return {object}
   */
  createAdapter() {
    return {};
  }

  init() {
  }

  render() {
    this.addEventListeners();
    this.loadData();
  }

  unmount() {
    this.removeEventListeners();
  }

  // 
  addEventListeners() {
    this.element.addEventListener('click', this.handleSwitchPage);
    this.element.addEventListener('change', this.handleChangePerPage);
  }

  // 
  removeEventListeners() {
    this.element.removeEventListener('click', this.handleSwitchPage);
    this.element.addEventListener('change', this.handleChangePerPage);
  }

  handleSwitchPage = (evt) => {
    evt.preventDefault();
    const {target} = evt;

    const {classList} = target;

    if (classList.contains(classes.PAGING_ITEM)) { // 
      const pageNum = parseInt(target.dataset.pagenum, 10);
      this.handleChangePage(pageNum);
    } else if (classList.contains('js-jump-btn')) {
      const input = target.parentNode.previousElementSibling.children[0].children[0];
      const pageNum = parseInt(input.value, 10);
      if (!Number.isNaN(pageNum)) {
        this.handleChangePage(pageNum);
      }
    }
  };

  handleChangePerPage = (evt) => {
    evt.preventDefault();
    const {target} = evt;

    const {classList} = target;

    if (classList.contains('select-inner')) { // 
      const pageSize = parseInt(target.value, 10);
      this.pageSize = pageSize;
      this.loadData();
    }
  };

  loadData(pageNum = 1) {
    // 
    if (this.loadPageData && typeof this.loadPageData === 'function') {
      this.loadPageData(pageNum, this.pageSize).then((json) => {
        const {totalCount, totalPages} = json.data;
        this.totalPages = totalPages;
        this.pageNum = pageNum;
        this.totalCount = totalCount;
        this.renderPaging();
      });
    } else {
      this.pageNum = pageNum;
      this.renderPaging();
    }
  }

  handleChangePage(pageNum) {
    if (this.totalPages === 1) {
      return;
    }
    if (pageNum <= 1) {
      pageNum = 1;
    }

    if (pageNum >= this.totalPages) {
      pageNum = this.totalPages;
    }

    this.loadData(pageNum);
  }

  /**
   * 
   * @returns {Array}
   */
  calculatePage() {
    const totalPages = this.totalPages;
    const pageNum = this.pageNum;

    const pageArray = [];
    if (totalPages < 8) {
      for (let i = 1; i <= totalPages; i++) {
        pageArray.push(i);
      }
    } else {
      pageArray.push(1);
      if (pageNum > 4) {
        pageArray.push('...');
      }

      if (pageNum < 4) {
        for (let i = 2; i <= 6; i++) {
          pageArray.push(i);
        }
      } else if (pageNum >= 4 && totalPages - pageNum >= 3) {
        for (let i = pageNum - 2; i <= pageNum + 2; i++) {
          pageArray.push(i);
        }
      } else {
        for (let i = totalPages - 4; i < totalPages; i++) {
          pageArray.push(i);
        }
      }

      // -   3 
      if (totalPages - pageNum > 3) {
        pageArray.push('...');
      }
      pageArray.push(totalPages);
    }

    return pageArray;
  }

  renderPaging() {
    const pageArray = this.calculatePage();

    let html = '<ul class="paging-items">';
    html += `<li class="paging-item${this.pageNum === 1 ? ` ${classes.DISABLED}` : ''}" data-pagenum="${this.pageNum - 1}"></li>`;
    const pageItems = pageArray.map((item, index) => {
      if (item === '...') {
        return '<li class="paging-item paging-more"></li>';
      }
      return `<li class="paging-item${this.pageNum === item ? ` ${classes.ACTIVE}` : ''}" data-pagenum="${item}">${item}</li>`;

    });
    html += pageItems.join('');
    html += `<li class="paging-item${this.pageNum === this.totalPages ? ` ${classes.DISABLED}` : ''}" data-pagenum="${this.pageNum + 1}"></li>`;
    html += '</ul>';

    if (this.pagingControl) {
      html += this.renderPagingControl();
    }
    this.element.innerHTML = html;
  }

  renderPagingControl() {
    let html = '<ul class="paging-control">';
    html += `<li class="paging-control-item">${this.totalPages}${this.totalCount || 0},</li>`;

    if (this.recordPerPage) {
      html += `<li class="paging-control-item">
        
        <div class="select">
        <select class="select-inner">
          <option value="5"${this.pageSize === 5 ? ' selected' : ''}>5</option>
          <option value="10"${this.pageSize === 10 ? ' selected' : ''}>10</option>
          <option value="20"${this.pageSize === 20 ? ' selected' : ''}>20</option>
          <option value="50"${this.pageSize === 50 ? ' selected' : ''}>50</option>
          <option value="100"${this.pageSize === 100 ? ' selected' : ''}>100</option>
        </select>
        </div>
      ,
    </li>`;
    }

    if (this.jumpControl) {
      html += `<li class="paging-control-item">
        
        <div class="input">
          <input type="text" class="input-field"/>
        </div>
        
      </li>
      <li class="paging-control-item">
        <a href="" class="btn btn-raised btn-primary btn-sm js-jump-btn"></a>
      </li>`;
    }

    html += '</ul>';

    return html;
  }
}

export default Paging;
