/**
 * 
 */
class Component {

  /**
   * 
   * 
   * @param element
   * @returns {Component}
   */
  static mount(element) {
    return new Component(element);
  }

  constructor(element, config = {}) {
    this.element = element;
    //  config  this 
    Object.keys(config).forEach((key) => {
      this[key] = config[key];
    });

    this.init();
  }

  init() {
    // 
    // 
  }

  // 
  unmount() {
    // 
  }

  /**
   * 
   * @param evtType
   * @param handler
   */
  listen(evtType, handler) {
    this.element.addEventListener(evtType, handler);
  }

  /**
   * 
   * @param evtType
   * @param handler
   */
  unlisten(evtType, handler) {
    this.element.removeEventListener(evtType, handler);
  }

  /**
   * 
   * @param evtType 
   * @param detail 
   * @param bubbles 
   * @param cancelable 
   */
  emit(evtType, detail, bubbles = false, cancelable = false) {
    let evt;
    if (typeof CustomEvent === 'function') {
      evt = new CustomEvent(evtType, {
        detail,
        bubbles,
        cancelable,
      });
    } else {
      evt = document.createEvent('CustomEvent');
      evt.initCustomEvent(evtType, bubbles, cancelable, detail);
    }

    this.element.dispatchEvent(evt);
  }
}

export default Component;
